/* frontend/src/components/InputDialog.css */
.input-dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.input-dialog {
  background-color: #2d2d30;
  border: 1px solid #3e3e42;
  border-radius: 8px;
  min-width: 400px;
  max-width: 90vw;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  animation: slideIn 0.2s ease-out;
}

@keyframes slideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.input-dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid #3e3e42;
  background-color: #37373d;
  border-radius: 8px 8px 0 0;
}

.input-dialog-header h3 {
  margin: 0;
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
}

.input-dialog-close {
  background: none;
  border: none;
  color: #cccccc;
  font-size: 18px;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 4px;
  transition: all 0.2s;
}

.input-dialog-close:hover {
  background-color: #e74c3c;
  color: #ffffff;
}

.input-dialog-form {
  padding: 20px;
}

.input-dialog-input {
  width: 100%;
  background-color: #3c3c3c;
  border: 1px solid #5a5a5a;
  color: #ffffff;
  padding: 12px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-family: inherit;
  margin-bottom: 20px;
  transition: border-color 0.2s;
}

.input-dialog-input:focus {
  outline: none;
  border-color: #007acc;
  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.2);
}

.input-dialog-input::placeholder {
  color: #888888;
}

.input-dialog-buttons {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.input-dialog-cancel,
.input-dialog-confirm {
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
  min-width: 80px;
}

.input-dialog-cancel {
  background-color: #6c757d;
  color: #ffffff;
}

.input-dialog-cancel:hover {
  background-color: #5a6268;
}

.input-dialog-confirm {
  background-color: #007acc;
  color: #ffffff;
}

.input-dialog-confirm:hover:not(:disabled) {
  background-color: #005a9e;
}

.input-dialog-confirm:disabled {
  background-color: #666666;
  cursor: not-allowed;
  opacity: 0.6;
}

/* Mobile responsive */
@media (max-width: 600px) {
  .input-dialog {
    min-width: 90vw;
    margin: 20px;
  }
  
  .input-dialog-header {
    padding: 12px 16px;
  }
  
  .input-dialog-form {
    padding: 16px;
  }
  
  .input-dialog-buttons {
    flex-direction: column;
  }
  
  .input-dialog-cancel,
  .input-dialog-confirm {
    width: 100%;
  }
}
