/* frontend/src/components/FileExplorer.css */
.file-explorer {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #252526;
}

.explorer-header {
  height: 35px;
  background-color: #2d2d30;
  border-bottom: 1px solid #3e3e42;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 12px;
  flex-shrink: 0;
}

.explorer-header h3 {
  margin: 0;
  color: #ffffff;
  font-size: 13px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.explorer-actions {
  display: flex;
  gap: 4px;
}

.action-btn {
  background: none;
  border: none;
  color: #cccccc;
  padding: 4px;
  cursor: pointer;
  border-radius: 3px;
  font-size: 14px;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
}

.action-btn:hover {
  background-color: #3e3e42;
  color: #ffffff;
}

.current-path {
  padding: 8px 12px;
  background-color: #2d2d30;
  border-bottom: 1px solid #3e3e42;
  font-size: 11px;
  color: #cccccc;
  font-family: monospace;
  word-break: break-all;
}

.files-list {
  flex: 1;
  overflow-y: auto;
  padding: 4px 0;
}

.empty-folder {
  padding: 20px 12px;
  text-align: center;
  color: #888888;
  font-size: 12px;
  font-style: italic;
}

.file-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 6px 12px;
  cursor: pointer;
  transition: background-color 0.2s;
  border-left: 3px solid transparent;
  font-size: 13px;
}

.file-item:hover {
  background-color: #2d2d30;
}

.file-item.clickable:hover {
  background-color: #37373d;
  border-left-color: #007acc;
}

.file-item.directory {
  font-weight: 500;
}

.file-info {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
  min-width: 0;
}

.file-icon {
  flex-shrink: 0;
  font-size: 14px;
  width: 16px;
  text-align: center;
}

.file-name {
  color: #cccccc;
  truncate: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.file-item.directory .file-name {
  color: #ffffff;
}

.file-meta {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-size: 10px;
  color: #888888;
  gap: 2px;
  flex-shrink: 0;
  margin-left: 8px;
}

.file-size {
  font-family: monospace;
}

.file-date {
  font-family: monospace;
}

.loading,
.error {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  color: #cccccc;
  font-size: 12px;
  text-align: center;
  padding: 20px;
}

.error {
  color: #dc3545;
  flex-direction: column;
  gap: 12px;
}

.refresh-btn {
  background-color: #007acc;
  border: none;
  color: #ffffff;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 11px;
  transition: background-color 0.2s;
}

.refresh-btn:hover {
  background-color: #005a9e;
}

/* Scrollbar Styling */
.files-list::-webkit-scrollbar {
  width: 8px;
}

.files-list::-webkit-scrollbar-track {
  background: #252526;
}

.files-list::-webkit-scrollbar-thumb {
  background: #5a5a5a;
  border-radius: 4px;
}

.files-list::-webkit-scrollbar-thumb:hover {
  background: #6a6a6a;
}

/* File Type Specific Styling */
.file-item .file-name {
  color: #cccccc;
}

.file-item[data-extension=".js"] .file-name,
.file-item[data-extension=".jsx"] .file-name {
  color: #f7df1e;
}

.file-item[data-extension=".ts"] .file-name,
.file-item[data-extension=".tsx"] .file-name {
  color: #3178c6;
}

.file-item[data-extension=".py"] .file-name {
  color: #3776ab;
}

.file-item[data-extension=".css"] .file-name {
  color: #1572b6;
}

.file-item[data-extension=".html"] .file-name {
  color: #e34c26;
}

.file-item[data-extension=".json"] .file-name {
  color: #85c46c;
}

.file-item[data-extension=".md"] .file-name {
  color: #ffffff;
}

/* Responsive Design */
@media (max-width: 768px) {
  .file-meta {
    display: none;
  }
  
  .file-item {
    padding: 8px 12px;
  }
  
  .file-info {
    gap: 6px;
  }
  
  .explorer-header {
    padding: 0 8px;
  }
  
  .current-path {
    padding: 6px 8px;
    font-size: 10px;
  }
}

@media (max-width: 480px) {
  .explorer-header h3 {
    font-size: 12px;
  }
  
  .file-item {
    padding: 6px 8px;
  }
  
  .file-info {
    gap: 4px;
  }
  
  .file-icon {
    font-size: 12px;
    width: 14px;
  }
  
  .file-name {
    font-size: 12px;
  }
}

/* Add to existing FileExplorer.css */

.navigation-bar {
  display: flex;
  gap: 4px;
  padding: 6px 12px;
  background-color: #2d2d30;
  border-bottom: 1px solid #3e3e42;
}

.nav-btn {
  background: none;
  border: 1px solid #5a5a5a;
  color: #cccccc;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 3px;
  font-size: 12px;
  transition: all 0.2s;
  min-width: 28px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-btn:hover:not(:disabled) {
  background-color: #3e3e42;
  color: #ffffff;
}

.nav-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.workspace-indicator {
  color: #007acc;
  font-size: 12px;
  margin-left: 12px;
  padding: 2px 8px;
  background-color: rgba(0, 122, 204, 0.1);
  border-radius: 4px;
  border: 1px solid rgba(0, 122, 204, 0.3);
}

/* Update existing action-btn styles */
.action-btn {
  background: none;
  border: none;
  color: #cccccc;
  padding: 3px;
  cursor: pointer;
  border-radius: 3px;
  font-size: 12px;
  transition: all 0.2s;
  display: flex;
